import JSZip from 'jszip';
import { saveAs } from 'file-saver';

/**
 * ZIP
 * @param file ZIP
 * @returns PromiseURL
 */
export async function extractImagesFromZip(file: File): Promise<{ url: string, name: string }[]> {
  console.log(`ZIP: ${file.name}, : ${(file.size / 1024 / 1024).toFixed(2)}MB`);
  
  try {
    const zip = new JSZip();
    const loadedZip = await zip.loadAsync(file);
    const images: { url: string, name: string }[] = [];
    const errors: string[] = [];
    
    const filenames = Object.keys(loadedZip.files)
      .filter(filename => {
        // 
        const lowerFilename = filename.toLowerCase();
        const isDir = loadedZip.files[filename].dir;
        const isImage = (
          lowerFilename.endsWith('.png') || 
          lowerFilename.endsWith('.jpg') || 
          lowerFilename.endsWith('.jpeg') || 
          lowerFilename.endsWith('.gif')
        );
        
        if (isDir) {
          console.log(`: ${filename}`);
        } else if (!isImage) {
          console.log(`: ${filename}`);
        }
        
        return !isDir && isImage;
      });
    
    console.log(`ZIP ${filenames.length} `);
    
    // 
    if (filenames.length === 0) {
      throw new Error('ZIPZIP.png, .jpg, .jpeg.gif');
    }
    
    // Promise.allSettledPromise.all
    const results = await Promise.allSettled(
      filenames.map(async (filename) => {
        try {
          const content = await loadedZip.files[filename].async('blob');
          
          // blob
          if (content.size === 0) {
            throw new Error(` ${filename} `);
          }
          
          const name = filename.split('/').pop() || filename;
          const url = URL.createObjectURL(content);
          
          // URL
          if (!url.startsWith('blob:')) {
            throw new Error(` ${filename} URL`);
          }
          
          // 
          await new Promise<void>((resolve, reject) => {
            const img = new Image();
            img.onload = () => {
              if (img.width === 0 || img.height === 0) {
                reject(new Error(` ${filename} `));
              } else {
                console.log(`: ${name}, : ${img.width}x${img.height}`);
                resolve();
              }
            };
            
            img.onerror = () => {
              reject(new Error(` ${filename} `));
            };
            
            // 
            const timeout = setTimeout(() => {
              reject(new Error(` ${filename} `));
            }, 5000);
            
            img.onload = () => {
              clearTimeout(timeout);
              console.log(`: ${name}, : ${img.width}x${img.height}`);
              resolve();
            };
            
            img.onerror = () => {
              clearTimeout(timeout);
              reject(new Error(` ${filename} `));
            };
            
            img.src = url;
          });
          
          return { url, name };
        } catch (error) {
          console.error(`ZIP ${filename} :`, error);
          throw error;
        }
      })
    );
    
    // 
    results.forEach((result, index) => {
      if (result.status === 'fulfilled') {
        images.push(result.value);
      } else {
        const filename = filenames[index];
        errors.push(`${filename}: ${result.reason?.message || ''}`);
      }
    });
    
    // 
    console.log(` ${images.length} ${errors.length} `);
    
    if (images.length === 0 && errors.length > 0) {
      throw new Error(`: ${errors.join('; ')}`);
    }
    
    return images;
  } catch (error) {
    console.error('ZIP:', error);
    throw error;
  }
}

/**
 * ZIP
 * @param cardImages Blob
 */
export async function createAndDownloadZip(cardImages: { blob: Blob, name: string }[]): Promise<void> {
  const zip = new JSZip();
  
  // ZIP
  cardImages.forEach((card, index) => {
    zip.file(`card_${index + 1}.png`, card.blob);
  });
  
  // ZIP
  const content = await zip.generateAsync({ type: 'blob' });
  saveAs(content, 'dobble_cards.zip');
} 